import React, { Component } from 'react';
import Table from '../Components/Table';
import { SecondaryBtn } from '../Components/Btns';
import Register from '../Components/Register';
import Payments from '../Components/Payments';
import CreateAdmin from '../Components/CreateAdmin';
import Alert from '../Components/Alert';
import {firebaseAuth, firebaseDB} from '../firebase'
import { Redirect, Link } from 'react-router-dom';
import FontAwesome from 'react-fontawesome';
import Fade from 'react-reveal/Fade';
import Yakata from '../Components/Yakata';


class HomePage extends Component {

    state = {
        content: 'table',
        tableType: 'all',
        filter: '',
        openDashboard: false,
        alert: {
            on: false,
            type: '',
            message: ''
        },
        signOut: false,
        isVerified: this.props.isVerified,
        changePassword: false,
        isLoading: false,
        toggleFastUpdate: false,
        openSidebar: true
    }

    componentDidMount(){
        // if(this.state.isVerified===false) {
        //     this.verifyMail()
        // }
        // if(this.props.admin.team){
        //     alert(`You are a member of team ${this.props.admin.team}`)
        // }else {
        //     console.log(this.props.admin)
        //     const team = prompt('Enter your team here')
        //     firebaseDB.ref('admins').child(this.props.admin.key).update({
        //         team: team
        //     }).then(()=>{
        //         alert('team updated Succesfully')
        //     })
        // }
    }

    verifyMail = () => {
        const user = firebaseAuth.currentUser;

        user.sendEmailVerification().then(()=> {
            console.log('sent')
        }).catch((error)=> {
            console.log('no way', error)
        });
    }

    // filter function handler
    search = (e)=>{
        const filter = e.target.value
        this.setState({filter})
    }
    

    // server alert hANDLER
    alert = (type, message)=> {
        this.setState({alert: {
            on: true,
            type,
            message
        }})
        setTimeout(() => {
            this.setState({alert:{
                on: false
            }})
        }, 5000);
    }

    //-- Table content handlers
    allInvestors = () => {
        
        if(window.innerWidth<650){
            this.setState({openSidebar: false})
        }
        this.setState({
            content: 'table',
            tableType: 'all'
        })
    }

    dueNextWeek = () => {
        
        if(window.innerWidth<650){
            this.setState({openSidebar: false})
        }
        this.setState({
            content: 'table',
            tableType: 'nextweek'
        })
    }

    dueThisWeek = () => {
        
        if(window.innerWidth<650){
            this.setState({openSidebar: false})
        }
        this.setState({
            content: 'table',
            tableType: 'thisweek'
        })
    }

    dueInLessThanTwoDays = () => {
        
        if(window.innerWidth<650){
            this.setState({openSidebar: false})
        }
        this.setState({
            content: 'table',
            tableType: 'lessthantwo'
        })
    }

    dueForPayment = () => {
        
        if(window.innerWidth<650){
            this.setState({openSidebar: false})
        }
        this.setState({
            content: 'table',
            tableType: 'due'
        })
    }

    fastUpdate = () => {
        
        if(window.innerWidth<650){
            this.setState({openSidebar: false})
        }
        this.setState({
            content: 'table',
            tableType: 'fastupdate'
        })
    }

    bankless = () => {
        
        if(window.innerWidth<650){
            this.setState({openSidebar: false})
        }
        this.setState({
            content: 'table',
            tableType: 'bankless'
        })
    }
    scammers = () => {
        
        if(window.innerWidth<650){
            this.setState({openSidebar: false})
        }
        this.setState({
            content: 'table',
            tableType: 'overdue'
        })
    }
    //--

    // toogle password change
    openPasswordChange = () => {
        if(this.state.changePassword){
            this.setState({changePassword: false})
        }else {
            this.setState({changePassword: true})
        }
    }

    // admin password change handler
    authUser = (e, admin)=>{
        e.preventDefault()
        const oldPassword = e.target.old.value
        const newPassword = e.target.new.value

        
        const user = firebaseAuth.currentUser;
        const credential = oldPassword

        // Prompt the user to re-provide their sign-in credentials
        this.setState({isLoading: true})
        firebaseAuth.signInWithEmailAndPassword(admin.email, oldPassword).then(()=> {
            
            user.updatePassword(newPassword).then(()=> {
                this.setState({
                    isLoading: false,
                    alert: {
                        on: true,
                        type: 'positive',
                        message: 'Password changed successfully'
                }})
              }).catch((error)=> {
                this.setState({
                    isLoading: false,
                    alert: {
                        on: true,
                        type: 'negative',
                        message: error.message
                }})
              });
        }).catch((error) => {
            this.setState({
                isLoading: false,
                alert: {
                    on: true,
                    type: 'negative',
                    message: error.message
            }})
        });
    }

    toggleFastUpdate = () => {
        if(this.state.toggleFastUpdate){
            this.setState({toggleFastUpdate: false})
        }else {
            this.setState({toggleFastUpdate: true})
            
        }
    }
    
    render() {
        const admin = this.props.admin

        return (
            <div className="homepage">
                
                {
                    // alert model
                    this.state.alert.on?
                    <Alert type={this.state.alert.type} 
                    message={this.state.alert.message}/>:null
                }

                {
                    this.state.openSidebar?
                        null:
                        <Fade left duration={1000}>
                            <button onClick={()=>this.setState({openSidebar: true})} className="open-sidebar">
                                <FontAwesome  style={{color: 'white', fontSize:'1.5rem'}} name="list" size="2x"/>
                            </button>
                        </Fade>
                }
                
                {/* homepage header */}
                <header className="header">
                    <div style={{display:'flex',alignItems:'center'}}>
                        <img alt="logo" className="logo" src={require('../img/logo.png')}/>
                        <h1 className="heading">Freedom Synergy pro</h1>
                    </div>
                    <button className="logout" onClick={this.props.logOut}>LOGOUT</button>
                </header>

                {
                    // welcome note for new admin
                    this.state.isVerified===false?
                    <p className="welcome">Welcome {admin.name}, we have sent a verification link to {admin.email}, please verify your email address <button onClick={this.verifyMail}>Resend mail</button></p>:null
                }

                {
                    // search model
                    this.state.content==='table'?
                        <section className="search">
                            {
                                admin.email==='ashsal.dev@gmail.com' || admin.email==='coolzyte@gmail.com' || admin.email==='bayosmith07@gmail.com' || admin.email==='oluwanifemi95debbie@gmail.com' || admin.email==='olasupolanre2009@gmail.com'  || admin.email==='uthmanyahya2@gmail.com' || admin.email==='tosingodwin09@gmail.com' || admin.email==='jerryoderinu381@gmail.com' || admin.email==='domwizzdominic@gmail.com'?
                                <input onChange={this.search} placeholder="Filter by name or userID..."/>:null
                            }
                            
                            {
                                admin.accountType === 'readandwrite'?
                                <div className="switch-container">
                                    <label>
                                        {
                                            this.state.toggleFastUpdate?
                                            <div className="switch">
                                                <div className="ball"></div>
                                            </div>:
                                            <div className="switch">
                                                <div className="ball2"></div>
                                            </div>
                                        }
                                        <input onChange={this.toggleFastUpdate} type="checkbox"/>
                                    </label>
                                    
                                </div>:null
                            }
                        </section>:null
                }

                {/* main content model */}
                <section className="content">
                    <div className="main_content">
                        {/* table content handler */}
                        {
                            this.state.content === 'table'?
                            <Table toggleFastUpdate={this.state.toggleFastUpdate}
                             admin={admin} holidays2019={this.props.holidays2019}
                              holidays2020={this.props.holidays2020} 
                              filter={this.state.filter} 
                              alert={this.alert}
                            tableType={this.state.tableType} 
                            openDashboard={this.props.openDashboard}/>:
                            this.state.content === 'register'?
                            <Register admin={admin} alert={this.alert}/>:
                            this.state.content === 'create'?
                            <CreateAdmin alert={this.alert}/>:
                            this.state.content === 'yakata'?
                            <Yakata alert={this.alert}/>:
                            this.state.content === 'payments'?
                            <Payments admin={admin} alert={this.alert}/>:null
                        }
                    </div>

                    {/* sidebar model */}
                    {
                        this.state.openSidebar?
                            <Fade left duration={1000}>
                                <div className="side-bar">
                                    <button className="close" onClick={()=>this.setState({openSidebar: false})}>X</button>
                                    <div className="info" onClick={this.allInvestors}>
                                        <h2>All investors</h2>
                                    </div>
                                    <div className="info" onClick={this.dueNextWeek}>
                                        <h2>Due for payment Next week</h2>
                                    </div>
                                    <div className="info" onClick={this.dueThisWeek}>
                                        <h2>Due for payment This week</h2>
                                    </div>
                                    {/* <div className="info" onClick={this.scammers}>
                                        <h2>Over due</h2>
                                    </div> */}
                                    <div className="info" onClick={this.dueForPayment}>
                                        <h2>Due but not paid</h2>
                                    </div>
                                    <div className="info">
                                        <Link to="/marketingboard">Marketers board &rarr;</Link>
                                    </div>
                                    {
                                        admin.accountType==='readandwrite' || admin.email==='uthmanyahya2@gmail.com' || admin.email==='adewoyinmotunrayo12@gmail.com'?
                                        <div className="info">
                                            <h2 onClick={()=> this.setState({content: 'payments'})}>Confirm Payment</h2>
                                        </div>:null
                                    }
                                    {
                                        this.state.toggleFastUpdate?
                                        <div className="info" onClick={this.fastUpdate}>
                                            <h2>Fast Update</h2>
                                        </div>:null
                                    }
                                    {
                                        admin.accountType==='readandwrite'?
                                        <div className="info" onClick={this.bankless}>
                                            <h2>No Bank</h2>
                                        </div>:null
                                    }
                                    
                                    
                                    <div className="cta">
                                        {
                                            admin.accountType === 'readandwrite'?
                                            <div>
                                                <SecondaryBtn onClick={()=> this.setState({content: 'register'})} title={'Register new investor'}/>
                                                <SecondaryBtn onClick={()=> this.setState({content: 'create'})} title={'Create new admin account'}/>
                                            </div>:
                                            admin.email === 'olasupolanre2009@gmail.com'?
                                            <div>
                                                <SecondaryBtn onClick={()=> this.setState({content: 'register'})} title={'Register new investor'}/>
                                            </div>:
                                            null
                                        }
                                        <div className="info" onClick={()=> this.setState({content: 'yakata'})}>
                                            <h2>Yakata</h2>
                                        </div>
                                        <div className="change_password">
                                        
                                            {/* password change toogle */}
                                            <button className="wrench" onClick={this.openPasswordChange}><FontAwesome className="font" name="wrench"/></button>
                                            {
                                                this.state.isLoading?
                                                <img src={require('../img/loader.jpeg')}/>:null
                                            }
                                        </div>
                                        {
                                            // password change model
                                            this.state.changePassword?
                                            <Fade top>
                                                <form onSubmit={(e)=>this.authUser(e, admin)}>
                                                    <input type="password" name="old" placeholder="Old password"/>
                                                    <input type="password" name="new" placeholder="New password"/>
                                                    <button>Reset password</button>
                                                </form>
                                            </Fade>:null
                                        }
                                    </div>
                                </div>
                            </Fade>
                        :null
                    }
                </section>
            </div>
        );
    }
}

export default HomePage;