import React, { Component } from 'react';
import Fade from 'react-reveal/Fade'
import Zoom from 'react-reveal/Zoom'
import { firebaseDB } from '../firebase';
import DaysLeft from '../Components/DaysLeft';
import moment from 'moment';
import business from 'moment-business-days'



class Overview extends Component {
    

    state = {
        user: {},
        regDate: {},
        finalDay: 0,
        finalDate: 0,
        currentStage: 0,
        currentPlan: '',
        totalEarning: 0,
        lastEarning: 0,
        lastEarningDate: 0,
        nextEarning: 0,
        upcomingEarning: {},
        isLoading: true,
        plans: {},
        holidays: [],
        numberOfHolidays: 0,
        calculationCount: 0,
        showHolidayDetails: false,
        daysLeft: '',
        dueDates: []
    }

    async componentDidMount(){
        let stage = this.props.user.Stage
        if(stage===undefined){
            stage=0
        }else if(typeof stage === "string") {
            stage = stage.split("")
            stage = parseInt(stage[0])
            
        }
        this.setState({currentStage: stage})

        ///// Fetching user details from firebase
        if(this.props.user.Name){
            await firebaseDB.ref('plans').on('value',snapshot=>{
                this.setState({plans: snapshot.val()})
            })
            this.setState({isLoading: true})
            firebaseDB.ref(`investors/${this.props.user.key}`).on('value',(snapshot)=>{
                this.setState({user: snapshot.val()})
                setTimeout(() => {
                    this.setState({isLoading: false})
                    this.calculateEarnings()
                    this.calculateUpcomingEarning()
                    this.getInvestmentPlan()
                }, 1000);
            })   
        }
    }

    getInvestmentPlan = () => {
        let currentPlan = this.state.user.invplan
        if(currentPlan==='p_38k'){
            currentPlan='38,000'
        }else if(currentPlan==='p_70k'){
            currentPlan='70,000'
        }else if(currentPlan==='p_140k'){
            currentPlan='140,000'
        }else if(currentPlan==='p_210k'){
            currentPlan='210,000'
        }else if(currentPlan==='p_280k'){
            currentPlan='280,000'
        }
        this.setState({currentPlan})
    }

 
    
    showHolidayDetails = () => {
        if(this.state.showHolidayDetails){
            this.setState({showHolidayDetails: false})
        }else {
            this.setState({showHolidayDetails: true})
        }
    }

    /// total earning handler
    calculateEarnings = () => {
        firebaseDB.ref('plans').on('value',snapshot=>{
            let plans = []
            plans = snapshot.val()

            let totalEarning = 0
            const currentStage = this.state.currentStage
            let plan = plans["p_38k"]
            if(this.props.user.invplan){
                plan = plans[this.props.user.invplan]
            }

            for(let i=0; i<currentStage; i++) {
                if(i < currentStage) {
                    totalEarning += plan[i]
                }
            }

            if(currentStage===0){
                this.setState({
                    totalEarning,
                    lastEarning: 0,
                    nextEarning: plan[currentStage],
                })
            }else {
                this.setState({
                    totalEarning,
                    lastEarning: plan[currentStage-1],
                    nextEarning: plan[currentStage],
                })
            }
        })
        

    }

    calculateUpcomingEarning = async () => {
        // converting reg date for api
        let regDate = this.state.user["Registration Date"].split("")
        if(Number.isInteger(parseInt(regDate[0]))) {
            regDate = this.state.user["Registration Date"].split("-")
            let year = parseInt(regDate[2])
            if(year<100){
                year+=2000
            }
            if(Number.isInteger(parseInt(regDate[1]))) {
                regDate = {
                    day: parseInt(regDate[0]),
                    month: parseInt(regDate[1]),
                    year,
                }
            }else {
                const months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec',]
                let month = regDate[1]
                month = months.findIndex(e=> e=== month)

                regDate = {
                    day: parseInt(regDate[0]),
                    month: month+1,
                    year,
                }
            }
            
        }else {
            const months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec',]
            regDate = this.state.user["Registration Date"].split(" ")
            let month = regDate[0]
            month = months.findIndex(e=> e=== month)

            let day = parseInt(regDate[1])
            let year = parseInt(regDate[2])
            if(year<100){
                year+=2000
            }
            regDate = {
                day: day,
                month: month+1,
                year,
            }
        }

        this.setState({regDate})

        // calculating days to be added to reg date
        let currentStage = this.state.currentStage
    
        if(Number.isNaN(currentStage)){
            currentStage=1
        }else if(this.state.user.account_type==='dnt' || this.state.user.Name.includes('DNT')){
            currentStage = 9
        }else {
            currentStage+=1
        }
        this.setState({currentStage: currentStage-1})

        let days
        const workingDays = 30
        
        days = (workingDays*currentStage)
        
        console.log(regDate)
        this.generateDueDates()
        this.getDate(regDate, days, false)
    }

    getDate=(regDate, days, one)=>{  
        console.log(days)
        const covidHoliday = 21

        const holidays = ['2019-01-01','2019-02-22','2019-04-19','2019-04-22','2019-05-01','2019-05-29',
        '2019-06-04','2019-06-05','2019-06-12','2019-08-12','2019-08-13','2019-10-01','2019-11-9',
        '2019-11-11','2019-12-25','2019-12-26','2020-01-01','2020-04-10','2020-04-13','2020-05-01',
        '2020-05-25','2020-05-26','2020-06-12','2020-07-30','2020-07-31','2020-10-01','2020-10-29',
        '2020-12-25','2020-12-26','2021-01-01','2021-04-02','2021-04-05','2021-05-01','2021-05-12',
        '2021-06-12','2021-07-19','2021-10-01','2021-10-18','2021-12-25','2021-12-26',   
        ]
        moment.updateLocale('us', {
            holidays,
            holidayFormat: 'YYYY-MM-DD',
        });
        

        const regDateString = `${regDate.day}-${regDate.month}-${regDate.year}`
        let ue
        // m.add('d', days)

        if(regDate.year===2020 && regDate.month<=4) {
            if(regDate.month===4 && regDate.day>13){
                ue = moment(business(regDateString, 'DD-MM-YYYY').businessAdd(days)._d)
            } else {
                ue = moment(business(regDateString, 'DD-MM-YYYY').businessAdd(days+covidHoliday)._d)
            }
            
        }else if(regDate.year===2019){
            ue = moment(business(regDateString, 'DD-MM-YYYY').businessAdd(days+covidHoliday)._d)    
        } else {
            ue = moment(business(regDateString, 'DD-MM-YYYY').businessAdd(days)._d)
        } 
        
        let upcomingEarning = {
            day: ue.get('date'),
            month: ue.get('month')+1,
            year: ue.get('year'),
        }        

        if(one){
            this.state.dueDates.push(`${upcomingEarning.day}-${upcomingEarning.month}-${upcomingEarning.year}`)
        }else {
            this.setState({
                upcomingEarning,
            })

            this.getDaysLeft()   
        }    
    }

    // fetching holidays before next earning date
    getHolidays = (startDate, endDate) => {    
            
        const allHolidays = this.props.holidays
        const holidays = []
        let i 
        if(startDate.year===endDate.year){
            for (i=startDate.month; i<=parseInt(endDate.month); i++) {
                allHolidays.forEach(holiday => {
                    const date = holiday.date.split("-")
                    if(parseInt(date[1])===i){
                        if(i===endDate.month){
                            if(endDate.day>parseInt(date[2])) {
                                holidays.push(holiday)
                            }
                        }else if(i===startDate.month){
                            if(startDate.day<parseInt(date[2])) {
                                holidays.push(holiday)
                            }
                        }else{
                            holidays.push(holiday)
                        }
                            
                        
                        
                    }
                });
            }
        } else{
            for (i=startDate.month; i<=12; i++) {
                allHolidays.forEach(holiday => {
                    const date = holiday.date.split("-")
                    if(parseInt(date[1])===i){
                        if(i===endDate.month){
                            if(endDate.day>parseInt(date[2])) {
                                holidays.push(holiday)
                            }
                        }else if(i===startDate.month){
                            if(startDate.day<parseInt(date[2])) {
                                holidays.push(holiday)
                            }
                        }else{
                            holidays.push(holiday)
                        }
                            
                        
                        
                    }
                });
            }
            for (i=0; i<=parseInt(endDate.month); i++) {
                allHolidays.forEach(holiday => {
                    const date = holiday.date.split("-")
                    if(parseInt(date[1])===i){
                        if(i===endDate.month){
                            if(endDate.day>parseInt(date[2])) {
                                holidays.push(holiday)
                            }
                        }else if(i===startDate.month){
                            if(startDate.day<parseInt(date[2])) {
                                holidays.push(holiday)
                            }
                        }else{
                            holidays.push(holiday)
                        }
                            
                    }
                });
            }
        }
        
        const numberOfHolidays = Math.ceil(holidays.length/2)
        this.setState({
            numberOfHolidays,
            holidays,
        })
            
        console.log(holidays)
                
        if(this.state.calculationCount<3){
            this.calculateUpcomingEarning()
            console.log(this.state.upcomingEarning)
        }
        
    }

    // calculating days left to next earning
    getDaysLeft = () => {

        let upcomingEarning = this.state.upcomingEarning
        const getDate = new Date()
        const date = {
            day: getDate.getUTCDate(),
            month: getDate.getUTCMonth()+1,
            year: getDate.getUTCFullYear()
        }

        const todayString = `${date.day}-${date.month}-${date.year}`
        const upEarningString = `${upcomingEarning.day}-${upcomingEarning.month}-${upcomingEarning.year}`

        var upEarning = moment(upEarningString, 'DD-MM-YYYY'); 
        var today = moment(todayString, 'DD-MM-YYYY');
        const daysLeft = upEarning.diff(today, 'days');

        console.log(daysLeft)

        this.setState({daysLeft})
    }

    generateDueDates = () => {
        const regDate = this.state.regDate

        let days
        const workingDays = 30
        
        let n = 1
        for(n; n<=9; n++) {
            days = (workingDays*n)
            this.getDate(regDate, days, true)
        }
    }

    render() {

        const lastEarningDate = this.state.lastEarningDate 
        const upcomingEarning = this.state.upcomingEarning 
        const admin = this.props.admin

        return (
            <Fade duration={1000}>
                <div className="overview">
                    <div className="details">
                        <p><span>Name:</span> {this.state.user.Name}</p>
                        {
                            admin.accountType==='readandwrite'?
                            <p><span>Phone:</span> {this.state.user["Phone Number"]}</p>:
                            null
                        }
                        {
                            admin.accountType==='readandwrite'?
                            <p><span>User ID:</span> {this.state.user["User ID"]}</p>:
                            null
                        }
                        <p><span>Username:</span> {this.state.user["User name"]}</p>
                        <p><span>Registration date:</span> {this.state.user["Registration Date"]}</p>
                        <p><span>Email Address:</span> {this.state.user["EMAIL ADDRESS"]}</p>
                        {
                            admin.accountType==='readandwrite'?
                            <div>
                                <p><span>Bank details:</span>{this.state.user["BANK Acount Details"]}</p>
                                <p><span>Other Bank:</span>{this.state.user["\"OTHERS BANK DETAILS \""]}</p>
                            </div>
                            :
                            null
                        }

                                {
                                    // investment plan model
                                    this.state.user.invplan?
                                    <p className="invplan">₦{this.state.currentPlan}</p>:
                                    <p className="invplan">₦38,000</p>
                                }
                    </div>

                    {/* investment details model */}
                    <div className="investment_details">
                        <Zoom duration={1000} delay={500}>
                            <div className="infobox">
                                <p>Earnings <span>Total earnings from stage 1</span></p>
                                <span>{this.state.totalEarning}</span>
                            </div>
                        </Zoom>
                        <Zoom duration={1000} delay={1000}>
                            <div className="infobox">
                                <p>Last earning <span>Most recent earning</span></p>
                                <span>{this.state.lastEarning || 0}</span>
                            </div>
                        </Zoom>
                        <Zoom duration={1000} delay={1500}>
                            <div className="infobox">
                                <p>Next Earning <span>Next earning</span></p>
                                <span>{this.state.nextEarning}</span>
                            </div>
                        </Zoom>
                        <Zoom duration={1000} delay={2000}>
                            <div className="infobox">
                                <p>Upcoming earning <span>Due date for next earning</span></p>
                                {
                                    this.state.isLoading?
                                    'Loading ...':
                                    <span>
                                        {`${upcomingEarning.day}-${upcomingEarning.month}-${upcomingEarning.year}`}
                                    </span>
                                }
                                
                            </div>
                            {/* <p style={{fontSize:"1.3rem"}}>Number of holiday(s): 
                            <span style={{fontWeight:"900"}}>{this.state.numberOfHolidays}</span>   
                            <button onClick={this.showHolidayDetails}>details</button></p> */}
                        </Zoom>
            
                        {
                            // holidat details model
                            this.state.showHolidayDetails?
                                <div className="holiday_details">
                                    {
                                        this.state.holidays.map(holiday=>{
                                            return <div className="holiday">
                                                <p>Holiday: {holiday.name}</p>
                                                <p>Date: {holiday.date}</p>
                                            </div>

                                        })
                                    }
                                </div>
                            :null
                        }
                    </div>
                    {
                        this.state.isLoading?
                        "Loading...":
                        <DaysLeft daysLeft={this.state.daysLeft}/>
                    }
                    <div className="progressbar">
                        <div className="bar" style={{width: `${11.1 * this.state.currentStage}%`}}>
                        </div>
                        <div className="graduation">
                            <span style={{border: 'none'}}>stage 1</span>
                            <span>stage 2</span>
                            <span>stage 3</span>
                            <span>stage 4</span>
                            <span>stage 5</span>
                            <span>stage 6</span>
                            <span>stage 7</span>
                            <span>stage 8</span>
                            <span>stage 9</span>
                        </div>
                    </div>

                    {
                        this.state.user.account_type==='dnt' || (this.state.user.Name && this.state.user.Name.includes('DNT'))?
                        <div className="dates">
                            <div style={{padding: '1rem 2rem'}} className="date cleared">
                                <div className="cont">
                                    <p>{this.state.dueDates[8]}</p>
                                </div>
                            </div>
                        </div>:
                        <div className="dates">

                            {
                                this.state.dueDates.map((date, num)=>{
                                    return (
                                        <div>
                                        {
                                            this.state.user.planType==='yakata'?
                                            <div>
                                            {
                                                num<this.state.currentStage?
                                                <div className="date cleared">
                                                    {
                                                        num<8?
                                                        <div className="cont">
                                                            {
                                                                num===0?
                                                                <div className="span">{1}st</div>:
                                                                num===1?
                                                                <div className="span">{2}nd</div>:
                                                                num===2?
                                                                <div className="span">{3}rd</div>:
                                                                <div className="span">{num+1}th</div>
                                                            }
                                                            <p>{date}</p>
                                                        </div>:null
                                                    }
                                                </div>:
                                                <div className="date">
                                                    {
                                                        num<8?
                                                        <div className="cont">
                                                            {
                                                                num===0?
                                                                <div className="span">{1}st</div>:
                                                                num===1?
                                                                <div className="span">{2}nd</div>:
                                                                num===2?
                                                                <div className="span">{3}rd</div>:
                                                                <div className="span">{num+1}th</div>
                                                            }
                                                            <p>{date}</p>
                                                        </div>:null
                                                    }
                                                </div>
                                            }
                                        </div>:
                                        <div>
                                            {
                                                num<this.state.currentStage?
                                                <div className="date cleared">
                                                    <div className="cont">
                                                        {
                                                            num===0?
                                                            <div className="span">{1}st</div>:
                                                            num===1?
                                                            <div className="span">{2}nd</div>:
                                                            num===2?
                                                            <div className="span">{3}rd</div>:
                                                            <div className="span">{num+1}th</div>
                                                        }
                                                        <p>{date}</p>
                                                    </div>
                                                </div>:
                                                <div className="date">
                                                    <div className="cont">
                                                        {
                                                            num===0?
                                                            <div className="span">{1}st</div>:
                                                            num===1?
                                                            <div className="span">{2}nd</div>:
                                                            num===2?
                                                            <div className="span">{3}rd</div>:
                                                            <div className="span">{num+1}th</div>
                                                        }
                                                        <p>{date}</p>
                                                    </div>
                                                </div>
                                            }
                                        </div>

                                        }
                                        </div>
                                    )
                                })
                            }
                        </div>
                    }
                </div>
            </Fade>
        );
    }
}


export default Overview;