    updateAll = () => {
        const now = moment()
        const dateUpdated = now.toString()

        this.setState({isLoading: true})
        const investors = this.state.fastUpdate
        investors.forEach(investor=>{
            let stage = investor.Stage
            if(stage===undefined || stage===''){
                stage=0
            }else if(typeof stage === "string") {
                stage = stage.split("")
                stage = parseInt(stage[0])   
            }

            const newStage = stage+1
            firebaseDB.ref(`investors/${investor.key}`).update({
                Stage: newStage,
                dateUpdated,
                
            }).then(e=>{
                firebaseDB.ref(`investors/${investor.key}`).child('primusTransaction').push().set({
                    amount: investor.nextEarningAmount,
                    stage: investor.Stage+1,
                    date: moment()
                }).then(e=>{
                    this.setState({isLoading: false})
                })
                this.setState({isLoading: false})
            })
        })
    }


