    ///// push investor for payment
    pushForPayment = (investor, i) => {

        const indexNumber = this.state.payRoll.findIndex(e=> e===investor)
            if(indexNumber>=0){
                this.state.payRoll.splice(indexNumber, 1)
                this.setState({priceToPay: this.state.priceToPay-investor.nextEarningAmount})
                const investors = this.state.filterSearch
                investors[i].onPayRoll=false
                this.setState({filterSearch: investors})
    
            }else {
                this.state.payRoll.push(investor)
                this.setState({priceToPay: this.state.priceToPay+investor.nextEarningAmount})
                const investors = this.state.filterSearch
                investors[i].onPayRoll=true
                this.setState({filterSearch: investors})
                console.log(investor.nextEarningAmount)
            }
            
        console.log(this.state.payRoll)
    }

    ////// generate payment naration
    generateNaration = () => {
        const payRoll = []
        this.state.payRoll.forEach(inv=>{
            let stage = inv.stage+1
            if(stage === 1){
                stage = 'First'
            }else if(stage===2){
                stage = 'Second'
            }else if(stage===3){
                stage = 'Third'
            }else if(stage===4){
                stage = 'Fourth'
            }else if(stage===5){
                stage = 'Fifth'
            }else if(stage===6){
                stage = 'Sixth'
            }else if(stage===7){
                stage = 'Seventh'
            }else if(stage===8){
                stage = 'Eighth'
            }else if(stage===9){
                stage = 'Ninth/Final'
            }
            payRoll.push({...inv, naration: `${stage} stage bonus from FSPRO`})
        })
        this.setState({payRoll})
        console.log(this.state.payRoll)
    }


