    filterByDay = (day) => {
        this.setState({priceToPay: 0, payRoll: []})

        const invs = this.state.investors
        let priceToPay = 0
        const payRoll = []
        invs.forEach((investor, i) => {
            let upcomingEarning = investor.upcomingEarning
            upcomingEarning = `${upcomingEarning.year}-${upcomingEarning.month}-${upcomingEarning.day}` 

            if(this.state.ignitePayment && investor.Stage<=7 && (investor.bank !== '' && 
            investor.bank !== undefined) && (investor.acc_no !== '' && investor.acc_no !== undefined && investor.Name.toLowerCase().indexOf("pend"))){
                if(moment(upcomingEarning).isSame(day, 'day')){

                    const indexNumber = this.state.payRoll.findIndex(e=> e.Name===investor.Name)

                    if(indexNumber>=0){
                        this.state.payRoll.splice(indexNumber, 1)
                        priceToPay -= investor.nextEarningAmount
                        
                        this.props.alert('positive', `${investor["User name"]} is already on the payment list`)
            
                    }else {
                        if(investor.Stage<8 && investor.nextEarningAmount > 0){
                            priceToPay += investor.nextEarningAmount
                            payRoll.push(investor)
                            this.props.alert('positive', 'Selection complete')
                        }
                    }
                        this.setState({priceToPay, payRoll})
                    
                }
            }

            
        })
        console.log(this.state.payRoll)
    }

