import React, { Component } from 'react';
import { firebaseDB } from '../firebase';

class SynerG extends Component {

    state = {
        investors: [],
        thismonth: [],
        plans: {},
        planLength: {}
    }

    componentDidMount(){
        const investors = []
        firebaseDB.ref('investors').on('value', snapshot => {
            snapshot.forEach(investor =>{
                investors.push(investor.val())
            })
            this.setState({investors})
            setTimeout(() => {
                this.getThisMonth()
                
            }, 1000);
        })
    }

    getThisMonth = () => {
        const thismonth = []
        this.state.investors.forEach(investor=>{
            if(investor["Registration Date"]){
                let regDate = investor["Registration Date"].split("")
                if(parseInt(regDate[0])>=0) {
                    regDate = investor["Registration Date"].split("-")
                    let year = parseInt(regDate[2])
                    if(year<100){
                        year+=2000
                    }
                    if(parseInt(regDate[1])>0) {
                        regDate = {
                            day: parseInt(regDate[0]),
                            month: parseInt(regDate[1]),
                            year,
                        }
                    }else {
                        const months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec',]
                        let month = regDate[1]
                        month = months.findIndex(e=> e=== month)
                        
                        regDate = {
                            day: parseInt(regDate[0]),
                            month: month+1,
                            year,
                        }
                    }
                    
                }else {
                    const months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec',]
                    regDate = investor["Registration Date"].split(" ")
                    let month = regDate[0]
                    month = months.findIndex(e=> e=== month)

                    let day = parseInt(regDate[1])
                    let year = parseInt(regDate[2])
                    if(year<100){
                        year+=2000
                    }

                    regDate = {
                        day: day,
                        month: month+1,
                        year,
                    }
                }
                if(regDate.month===9){
                    thismonth.push(investor)
                }
                this.setState({thismonth})

            }
        })
        console.log(this.state.thismonth)
        this.getPlans()
    }

    getPlans = () => {
        const p70k = []
        const p140k = []
        const p210k = []
        const p280k = []
        const p420k = []
        const p490k = []
        this.state.thismonth.forEach(investor=>{
            if(investor.invplan==='p_70k'){
                p70k.push(investor)
            }else if(investor.invplan==='p_140k'){
                p140k.push(investor)
            }else if(investor.invplan==='p_210k'){
                p210k.push(investor)
            }else if(investor.invplan==='p_280k'){
                p280k.push(investor)
            }else if(investor.invplan==='p_420k'){
                p420k.push(investor)
            }
            else if(investor.invplan==='p_490k'){
                p490k.push(investor)
            }
        })
        this.setState({plans: {
            p70k,
            p140k,
            p210k,
            p280k,
            p420k,
            p490k,
        }, planLength: {
            p70k: p70k.length,
            p140k: p140k.length,
            p210k: p210k.length,
            p280k: p280k.length,
            p420k: p420k.length,
            p490k: p490k.length,
        }})
        console.log(this.state.plans, p70k)

    }
    render() {
        return (
            <div className="synerg">
                <div style={{width: '80vw', margin:'auto', textAlign:'center'}}>
                    <img src={require('../img/logo.png')}/>
                    <h1 className="heading">Syner <span>G</span></h1>
                    <p>Synergy Graph</p>
                    <button className="get-graph">Get graph</button>
                </div>
                <div className="plans">
                    <div className="inv">
                        <p className="plan"> {this.state.planLength.p70k}</p>
                        <p>70,000</p>
                    </div>
                    <div className="inv">
                        <p className="plan">{this.state.planLength.p140k}</p>
                        <p>140,000</p>
                    </div>
                    <div className="inv">
                        <p className="plan">{this.state.planLength.p210k}</p>
                        <p>210,000</p>
                    </div>
                    <div className="inv">
                        <p className="plan">{this.state.planLength.p280k}</p>
                        <p>280,000</p>
                    </div>
                    <div className="inv">
                        <p className="plan">{this.state.planLength.p420k}</p>
                        <p>420,000</p>
                    </div>
                    <div className="inv">
                        <p className="plan">{this.state.planLength.p490k}</p>
                        <p>490,000</p>
                    </div>
                </div>
            </div>
        );
    }
}

export default SynerG;