const ChangeBank = ({user, alert}) => {
    const [account, setAccount] = useState("")
    const [bankCode, setBankCode] = useState(user.bankCode||'044')
    const [bankName, setBankName] = useState("")
    

    const submit = () => {
        
        fetch(`https://api.paystack.co/bank/resolve?account_number=${account.trim()}&bank_code=${bankCode}`, {
            method: 'get',
            headers: {
            'Authorization': `Bearer sk_live_3fd598928632736eaaa7467f3b428ca78ae02cfb`
            },}
            ).then(e=>{
                return e.json()
                .then(e=>{
                    console.log(e)

                    let string1 = user.Name
                    let bankname = e.data.account_name
                    setBankName(bankname)
                
                    if(string1.includes('(')){
                        let n = string1.split("(")
                        string1= n[0]
                    }
                    const nameLength = string1.split(" ").length
                    let string1ch = string1.split(" ").join("").toLowerCase()
                    let banknameArr = bankname.split(" ")
                
                    let num = 0
                
                    for (let i = 0; i<banknameArr.length; i++) {
                        if(string1ch.includes(banknameArr[i].toLowerCase())){
                            num+=1
                        }
                    } 
                    
                    const percent = (num/nameLength)*100
    
                    if(percent>=50){
                        alert('positive', "verified")
                        firebaseDB.ref('investors').child(user.key).update({
                            bankCode,
                            acc_no: account
                        })
                    }else {
                        alert("negative", "Not verified")
                    }


                })
            }).catch(e=>{
                console.log(e)
        })
        
    }

    return (
        <div className="changebank">
            <header>
                <h2>Change Account number</h2>
            </header>

            <div>
                <h3>Bank: {user.bank} </h3>
                <h3>Account Number: {user.acc_no} </h3>
            </div>
            <select name="bankCode" onChange={(e)=>setBankCode(e.target.value)} required defaultValue={user.bankCode}>
                <option value="">Select a bank</option>
                {
                    banks.map(e=><option value={e.code}>{e.id} - {e.name}</option>
                    )
                }
            </select>
            <input onChange={(e)=> setAccount(e.target.value)} placeholder="Account number"/>
            <h3 style={{color: 'orangegit s'}}>{bankName}</h3>
            <div onClick={submit}>
                <PrimaryBtn title="Save changes"/>
            </div>
        </div>
    );
};

export default ChangeBank;