import React, { Component } from 'react';
import Bounce from 'react-reveal/Bounce'
import Fade from 'react-reveal/Fade';
import {PrimaryBtn} from '../Components/Btns';
import { firebaseDB } from '../firebase';
import moment from 'moment';

class Payments extends Component {

    state = {
        payments: [],
        modeOfPayment: 'cash',
        number: 1,
        openForm: false,
        threat: false,
        currentTotal: 0,
        total: 0,
        isLoading: true,
        cashTotal: 0,
    }

    componentDidMount(){
        
        firebaseDB.ref('payments').once('value', (snapshot)=>{
            const payments = []
            snapshot.forEach(payment => {
                payments.push({...payment.val(), key: payment.key})
            });
            this.setState({payments, isLoading: false})
            console.log(this.state.payments)
            setTimeout(() => {
                this.calculateCurrentTotal()
                this.getCashTotal()
            }, 500);
        })
    }

    getCashTotal = () => {
        let total = 0
        this.state.payments.forEach(payment=>{
            if(payment.paymentmethod==='cash'){
                total += parseInt(payment.amount)
            }
        })
        this.setState({cashTotal: total})
    }

    openForm = () => {
        if(this.state.openForm){
            this.setState({openForm: false})
        }else {
            this.setState({openForm: true})
        }
    }

    calculateCurrentTotal = () => {
        let total = 0
        let currentTotal = 0
        this.state.payments.forEach(payment=>{
            total += parseInt(payment.amount)
            this.setState({total})
            if(moment(payment.moment).isSame(moment(), 'day')){
                currentTotal += parseInt(payment.amount)
                this.setState({currentTotal})
            }
        })
    }

    checkForThreat = (dataToCheck) => {
        this.state.payments.forEach(payment=>{
            if(payment.invName === dataToCheck || payment.paymentname===dataToCheck || payment.referenceNo===dataToCheck){
                return true
            }
        })
    }

    confirmPayment = (e) => {
        e.preventDefault()

        const date = moment()
        const fullMoment = date.toString()

        let paymentname
        let referenceNo
        if(this.state.modeOfPayment==='cash'){
            paymentname = '-'
            referenceNo = '-'
        }else {
            paymentname = e.target.paymentname.value
            referenceNo = e.target.reference.value
        }
        const userDetails = {
            invName: e.target.invname.value,
            paymentmethod: e.target.paymentmethod.value,
            amount: e.target.amount.value,
            paymentname,
            referenceNo,
            moment: fullMoment,
            LY: false,
            package: false
        }
        
        const isExisting = this.state.payments.find(function(payment) {
            if(userDetails.paymentmethod==='cash'){
                return payment.invName === userDetails.invName;
            }else {
                return payment.referenceNo === userDetails.referenceNo;
            }
        });

        if(isExisting){
            this.props.alert('negative', 'trying to duplicate a payment?')
            firebaseDB.ref('threat').push().set(userDetails)
        }else {
            firebaseDB.ref('payments').push().set(userDetails).then(()=>{
                this.props.alert('positive', 'Confirmation complete')
                document.querySelector('.rform').reset()
                this.setState({threat: false})
            }).catch(e=>{
                this.props.alert('negative', 'Confirmation failed')
            })
        }
    }

    updateConfirm = (type, payment) => {
        if(this.props.admin.email==='uthmanyahya2@gmail.com'){
            firebaseDB.ref('payments').child(payment.key).update({
                LY: true
            })
        }else if(this.props.admin.email==='adewoyinmotunrayo12@gmail.com'){
            if(payment.LY){
                firebaseDB.ref('payments').child(payment.key).update({
                    package: true
                })
            }else{
                this.props.alert('negative', 'Confirmation failed')
            }
        }else {
            this.props.alert('negative', 'You are not allowed to perform this action')
        }
        
        
    }
    
    delete = (payment) => {
        if(this.props.admin.email==='uthmanyahya2@gmail.com'){
            firebaseDB.ref('payments').child(payment.key).remove()
        }else {
            this.props.alert('negative', 'You are not allowed to perform this action')
        }
        
        
    }

    render() {
        return (
            <div className="payments">
                <h2 className="heading2">Payments</h2>
                <div className="currenttotal"><p>Total:</p>  ₦{this.state.total}</div>
                <div className="currenttotal"><p>Current Total:</p>  ₦{this.state.currentTotal}</div>
                <div className="currenttotal"><p>Bank Total:</p>  ₦{this.state.currentTotal-this.state.cashTotal}</div>
                <div className="currenttotal"><p>Cash Total:</p>  ₦{this.state.cashTotal}</div>

                <section className="table">
                    <table>
                        <tr>
                            <Bounce>
                                <th>Date</th>
                                <th>Name on payment</th>
                                <th>Name of Investor</th>
                                <th>Amount paid</th>
                                <th>Mode of Payment</th>
                                <th>Reference number</th>
                                <th>LY</th>
                                <th>Package</th>

                            </Bounce>
                        </tr>
                            {
                            this.state.payments.map(payment=>{
                                return (
                                    <Bounce right>
                                    {
                                        this.state.isLoading?
                                        <img className="loader" style={{width: '3rem', textAlign: 'center'}} src={require('../img/loader.jpeg')}/>:
                                        <tr>
                                            <td>{payment.moment}</td>
                                            <td>{payment.paymentname}</td>
                                            <td>{payment.invName}</td>
                                            <td>{payment.amount}</td>
                                            <td>{payment.paymentmethod}</td>
                                            <td>{payment.referenceNo}</td>
                                            <td style={{cursor: 'pointer'}} title="confirm" onClick={()=> this.updateConfirm('ly', payment)}>{
                                                payment.LY?
                                                <span>✔️</span>:                                                
                                                <span>❌</span>
                                            }</td>
                                            <td style={{cursor: 'pointer'}} title="confirm" onClick={()=> this.updateConfirm('package', payment)}>{
                                                payment.package?
                                                <span>✔️</span>:                                                
                                                <span>❌</span>
                                            }</td>
                                            <td><button onClick={()=> this.delete(payment)}>delete</button></td>
                                        </tr>
                                    }
                                    
                                        
                                    </Bounce>
                                )
                            })
                        }   
                        
                    </table>
                </section>
                {
                    this.state.openForm?
                        <Fade bottom duration={1000}>
                            <section className="form">
                                <div className="header" onClick={this.openForm}>
                                    <h3>Confirm new payment</h3>
                                    {
                                        this.state.openForm?
                                        <button>&times;</button>:
                                        <button>-</button>
                                    }
                                </div>
                                <form className="rform" onSubmit={this.confirmPayment}>
                                    <input onChange={this.checkForThreat} required name="invname" placeholder="Name of Investor"/>
                                    <select name="paymentmethod" required onChange={(e)=> this.setState({modeOfPayment: e.target.value})}>
                                        <option value="">Select a payment method</option>
                                        <option value="cash">Cash</option>
                                        <option value="transfer">Transfer</option>
                                        <option value="atm">ATM</option>
                                        <option value="pos">POS</option>
                                    </select>
                                    <input required name="amount" placeholder="Amount received"/>
                                    {
                                        this.state.modeOfPayment==='cash'?
                                        null:
                                        <div>
                                            <input onChange={this.checkForThreat} required name="paymentname"  placeholder="Name on payment"/>
                                            <input onChange={this.checkForThreat} required name="reference" placeholder="Reference number"/>
                                        </div>
                                    }
                                    <PrimaryBtn title={"Proceed"}/>
                                </form>
                            </section>
                        </Fade>:
                        <section className="form">
                            {
                                this.props.admin.accountType==='readandwrite' || this.props.admin.email==='uthmanyahya2@gmail.com'?
                                 <Fade bottom delay={500} duration={1000}>
                                    <div className="header" onClick={this.openForm}>
                                        <h3>Confirm new payment</h3>
                                        {
                                            this.state.openForm?
                                            <button>&times;</button>:
                                            <button>-</button>
                                        }
                                    </div>
                                </Fade>:null
                            }
                            
                        </section>
                }
            </div>
        );
    }
}

export default Payments;