import React, { Component } from 'react';

class PaymentHistory extends Component {

    state = {
        payments: [],
        details: {},
        empty: false
    }

    componentDidMount(){
        let payments
        if(this.props.user.payments){
            payments = Object.values(this.props.user.payments)
            
        }else {
            this.setState({empty: true})
        }
        this.setState({payments})
    }

    getTransfer = (code) => {
        fetch(`https://api.paystack.co/transfer/${code}`, {
            method: 'get',
            headers: {
                'Authorization': `Bearer sk_live_3fd598928632736eaaa7467f3b428ca78ae02cfb`
            }
        }).then(e=>{
            return e.json()
            .then(e=>{
                this.setState({details: e})
                console.log(e)
            })
        }).catch(e=>{
            console.log(e)
            
        })
    }

   

    render() {

        const { user } = this.props
        return (
            <div>
                {
                    this.state.empty?
                    'No payment History':
                    <div className="history">
                <div className="transactions">
                    <header>
                        <p>S/N</p>
                        <p>Currency</p>
                        <p>Amount</p>
                        <p>Recipient</p>
                        <p>Transfer code</p>
                    </header>
                    
                    {
                        this.state.payments.map((e, i)=>{
                            return <div onClick={()=> this.getTransfer(e.transfer_code)} className="transaction">
                                    <p>{i+1}</p>
                                    <p>{e.currency}</p>
                                    <p>{e.amount}</p>
                                    <p>{e.recipient}</p>
                                    <p>{e.transfer_code}</p>
                                </div>
                        })
                    }
                </div>
                {
                    this.state.details.data?
                    <div className="details">
                        <h2 className="name">Name: {user.Name}</h2>
                        <div className="info">
                            <p className="data">Date created:</p>
                            <p> {this.state.details.data.createdAt}</p>
                        </div>
                        <div className="info">
                            <p className="data">Amount:</p>
                            <p>NGN {this.state.details.data.amount}</p>
                        </div>
                        <div className="info">
                            <p className="data">ID:</p>
                            <p> {this.state.details.data.id}</p>
                        </div>
                        <div className="info">
                            <p className="data">Naration:</p>
                            <p> {this.state.details.data.reason}</p>
                        </div>
                        <div className="info">
                            <p className="data">Reference:</p>
                            <p> {this.state.details.data.reference}</p>
                        </div>
                        <div className="info">
                            <p className="data">Bank Name:</p>
                            <p> {this.state.details.data.recipient.details.bank_name}</p>
                        </div>
                        <div className="info">
                            <p className="data">Account Number:</p>
                            <p> {this.state.details.data.recipient.details.account_number}</p>
                        </div>
                        <div className="info">
                            <p className="data">Transaction type:</p>
                            <p> {this.state.details.data.recipient.type}</p>
                        </div>
                        <div className="info">
                            <p className="data">Transfered At:</p>
                            <p> {this.state.details.data.transferred_at}</p>
                        </div>
                        <div className="info">
                            <p className="data">Updated At:</p>
                            <p> {this.state.details.data.updatedAt}</p>
                        </div>

                        {
                            this.state.details.data.status==='success'?
                            <div className="status success">
                                <h2>successful</h2>
                            </div>:
                            this.state.details.data.status==='pending'?
                            <div className="status pending">
                                <h2>{this.state.details.data.status}</h2>
                            </div>:
                            <div className="status failed">
                                <h2>{this.state.details.data.status}</h2>
                            </div>
                        }
                    </div>:null
                }
            </div>
                }
            </div>
            
        );
    }
}

export default PaymentHistory;