import React, { Component } from 'react';
import FontAwesome from 'react-fontawesome';
import { firebaseDB, firebaseStorage } from '../firebase';
import { Redirect, Link } from 'react-router-dom';
import Fade from 'react-reveal/Fade'

class MarketingBoard extends Component {

    state={
        admins: [],
        admin: {},
        investors: [],
        target: 15,
        currentTotal: 0,
        converts: [],
        monthlyTotal: 0,
        monthlyConverts: [],
        totalLeft: 0,
        percentage: 0,
        daysLeft: 0,
        newTarget: 0,
        emoji: '',
        converts: [],
        lengthOfConverts: 0,
        isLoading: true,
        back: false,
        search: '',
        openAdmin: true,
        openSidebar: false,
    }

    componentDidMount(){
        this.getInvestors()
        this.getAdmins()
        this.getRemanningDays()
        if (this.state.admin.key){
            firebaseDB.ref('staffs').child(this.state.admin.key).on('value', e=>{
                this.setState({ 
                    admin:{...this.state.admin, url: e.val().url},
                    isLoading: false
                })
    
            })
        }
    }

    getAdmins = () => {
        const staffs = []
        firebaseDB.ref('staffs').once('value').then(snapshot=>{
            snapshot.forEach(staff=>{
                staffs.push(staff.val())
            })
            this.setState({admins: staffs, isLoading: false})
            console.log(this.state.admins, snapshot.val())
        })
    }
    
    getInvestors = () => {
        const investors = []
        firebaseDB.ref('investors').once('value').then(snapshot=>{
            snapshot.forEach(investor=>{
                investors.push(investor.val())
            })
        }).then(e=>{
            this.setState({investors, isLoading: false})
            console.log(this.state.investors)
        })
    }
    getEmoji = () => {
        const percent = this.state.percentage
        let emoji
        if(percent>=85){
            emoji = '😎'
        }else if(percent>=70){
            emoji = '😁'
        }else if(percent>=60){
            emoji = '👍'
        }else if(percent>=50){
            emoji = '🙂'
        }else if(percent>=40){
            emoji = '😶'
        }
        this.setState({emoji})
    }

    getRemanningDays = () => {
        var date = new Date();
        var time = new Date(date.getTime());
        time.setMonth(date.getMonth() + 1);
        time.setDate(0);
        var daysLeft = time.getDate() > date.getDate() ? time.getDate() - date.getDate() : 0;
        this.setState({daysLeft})
    }

    calculateGrades = () =>{
        let target = this.state.target
        // if(this.state.admin.target){
        //     this.setState({target:this.state.admin.target})
        //     target=this.state.admin.target
        // }

        const monthlyTotal = this.state.monthlyTotal

        let totalLeft
        if(target>monthlyTotal){
            totalLeft = target - monthlyTotal
        }else {
            totalLeft = monthlyTotal - target
        }
        this.setState({totalLeft})

        const percentage = ((monthlyTotal/target)*100).toFixed(1)
        this.setState({percentage})
    }

    calculateNewTarget = () => {
        const daysLeft = this.state.daysLeft
        const totalLeft = this.state.totalLeft

        let newTarget = `${Math.ceil(totalLeft/daysLeft)} per day`
        if(daysLeft===0){
            newTarget=`${totalLeft} today`
        }
        this.setState({newTarget})
    }

    getCurrentTotal = (admin) => {
        const converts = []
        if(admin.USERNAME){
            this.state.investors.forEach(investor=>{
                if((investor.acc_username && investor.acc_username.toUpperCase() === admin.USERNAME.toUpperCase()) || (investor.acc_username && investor.acc_username.toUpperCase().includes(admin.NAME.toUpperCase())) || (investor.accountOfficer && investor.accountOfficer.toUpperCase().includes(admin.NAME.toUpperCase())) ){
                    converts.push(investor)
                }
            })
        }
        this.setState({converts})
        const currentTotal = converts.length
        this.setState({
            admin,
            currentTotal,
            converts,
            openAdmin: true,
        })
        this.getMonthlyConverts()
            this.calculateGrades()
        setTimeout(() => {
            this.calculateNewTarget()
            this.getEmoji()
        }, 1000);
        console.log(admin, '......')
    }

    getMonthlyConverts = () => {
        const monthlyConverts = []
        this.state.converts.forEach(investor=>{
             ///// calculating registration month
             if(investor.formdate){
                let regDate = investor.formdate.split("")
                if(parseInt(regDate[0])>=0) {
                    regDate = investor.formdate.split("-")  
                    console.log(regDate, 'll')                              
                    if(parseInt(regDate[1])>0) {
                        regDate = {
                            day: parseInt(regDate[0]),
                            month: parseInt(regDate[1]),
                        }
                    }else {
                        const months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec',]
                        let month = regDate[1]
                        month = months.findIndex(e=> e=== month)
                        
                        regDate = {
                            day: parseInt(regDate[0]),
                            month: month+1,
                        }
                    }
                    
                }else {
                    const months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec',]
                    regDate = investor.formdate.split(" ")
                    let month = regDate[0]
                    month = months.findIndex(e=> e=== month)

                    let day = parseInt(regDate[1])
                    

                    regDate = {
                        day: day,
                        month: month+1,
                    }
                }
                console.log(regDate)
                
                if(regDate.month === new Date().getUTCMonth()+1){
                    monthlyConverts.push(investor)
                }
            }
        })
                this.setState({
                    monthlyConverts,
                    monthlyTotal: monthlyConverts.length
                })
    }

    checkFile = (file) => {
        const img = file.target.files[0]
        if(img.type.includes('image') && img.size <= 1000000){
            this.uploadPic(file)
        } else if (img.size > 2000000){
            alert("Sorry, you can only upload images of 1MB or less")
        } else {
            alert("Wrong file type, try uploading an image")
        }
    }

    uploadPic = (file) => {
        const img = file.target.files[0]

        firebaseStorage.ref(`admins/${this.state.admin.key}`).put(img).then(()=>{
            firebaseStorage.ref(`admins/${this.state.admin.key}`).getDownloadURL().then(url=>{
                firebaseDB.ref(`admins/${this.state.admin.key}`).update({ url }).then(e=>{
                    alert('Upload successful')
                })
                
            }).catch(e=>{
                alert('Uploaf failed try again')
                console.log(e)
            })
        }).catch(e=>{
            alert('negative, try again')
            console.log(e)
        })
    }

    checkFile = (file) => {
        const img = file.target.files[0]
        if(img.type.includes('image') && img.size <= 1000000){
            this.uploadPic(file)
        } else if (img.size > 1000000){
            alert('negative', "Sorry, you can only upload images of 1MB or less")
        } else {
            alert('negative', "Wrong file type, try uploading an image")
        }

    }

    uploadPic = (file) => {
        const img = file.target.files[0]

        firebaseStorage.ref('admins').child(this.state.admin.key).put(img).then(()=>{
            firebaseStorage.ref('admins').child(this.state.admin.key).getDownloadURL().then(url=>{
                firebaseDB.ref('admins').child(this.state.admin.key).update({ url })
                alert('positive')
            }).catch(e=>{
                alert('negative')
                console.log(e)
            })
        }).catch(e=>{
            alert('negative')
            console.log(e)
        })
    }

    filterList = (e) => {
        this.setState({search: e.target.value})
    }

    update = (e)=>{
        e.preventDefault()
        firebaseDB.ref(`admins/${this.state.admin.key}`).update({
            target: e.target.target.value
        }).then(e=>{
            console.log(e)
        })
    }

    render() {
        const filterSearch = this.state.admins.filter(admin=>{
            return admin.NAME.toLowerCase().includes(this.state.search.toLowerCase())
        })
        return (
            <div className="marketing-board">
                {
                    this.state.back?
                    <Redirect to="/admin"/>:null
                }

                {/* {
                    this.state.openSidebar?
                        null:
                        <Fade left duration={1000}>
                            <button onClick={()=>this.setState({openSidebar: true})} className="open-sidebar">
                                <FontAwesome  style={{color: 'white', fontSize:'1.5rem'}} name="list" size="2x"/>
                            </button>
                        </Fade>
                }
                 */}
                <header className="header">
                    <div style={{display:'flex',alignItems:'center'}}>
                        <img alt="logo" className="logo" src={require('../img/logo.png')}/>
                        <h1 className="heading">Freedom Synergy pro</h1>
                    </div>
                    <button className="back" onClick={()=>this.setState({back: true})}>Back to homepage</button>
                </header>
                <h2>Marketers Board</h2>
                <section className="body">

                    {/* main section */}
                    {
                        this.state.isLoading?                        
                        <img className="loader" style={{width: '5rem', marginLeft:'auto'}} src={require("../img/loader.jpeg")}/>:
                        <div style={{gridColumn: '1/6'}}>
                            {
                                this.state.openAdmin?
                                <Fade bottom duration={1000}>
                                    <section className="main">
                                            <div className="pic">
                                                {
                                                    this.state.admin.NAME?
                                                    <label>
                                                        <FontAwesome className="font" name="camera" size="2x"/>
                                                        <input onChange={this.checkFile} type="file"/>
                                                    </label>:null
                                                }
                                                {
                                                    this.state.isLoadingpic?

                                                    <img className="loader" src={require('../img/loader.jpeg')}/>
                                                    :
                                                    this.state.admin.url?
                                                    <img src={this.state.admin.url}/>:
                                                    <img src={require('../img/default.png')}/>
                                                }
                                            </div>
                                            <div className="details">
                                                <h3 className="name">{this.state.admin.NAME}</h3>
                                                <p className="role">{this.state.admin.USERNAME}</p>
                                            </div>
                                            <div className="details">
                                                <ul>
                                                    <li><span>Target:</span> {this.state.target}</li>
                                                    <li><span>Total:</span> {this.state.currentTotal}</li>
                                                    <li><span>Total this Month:</span> {this.state.monthlyTotal}</li>
                                                    <li><span>Total left:</span> {this.state.totalLeft}</li>
                                                    <li><span>Days left:</span> {this.state.daysLeft}</li>
                                                </ul>
                                            </div>
                                            <div className="progress-bar">
                                                <div className="block" style={{width: `${this.state.percentage}%`}}>
                                                    <span className="percentage"><span>{this.state.emoji}</span> {this.state.percentage}%</span>
                                                </div>
                                            </div>
                                            <p className="new-target">{this.state.newTarget}</p>


                                            {/* {
                                                this.state.admin.NAME===this.props.admin.NAME?
                                                <form onSubmit={this.update}>
                                                    <input name="target" type="number" placeholder="New target"/>
                                                
                                                    <PrimaryBtn title="Update" />
                                                </form>:null
                                            } */}

                                            <section className="converts">
                                                <div className="head">
                                                    <h2 className="heading">All</h2>
                                                    <button>+</button>
                                                </div>
                                                {
                                                    this.state.converts.map(investor=>{
                                                        return (
                                                            <div className="investor">
                                                                <p>{investor.Name}</p>
                                                                <p>{investor["User name"]}</p>
                                                                {
                                                                    investor.invplan?
                                                                    <p>₦{investor.invplan}</p>:
                                                                    <p>₦38,000</p>
                                                                }
                                                                <p>{investor.formdate}</p>
                                                            </div>
                                                        )
                                                    })
                                                }
                                            </section>
                                            <div className="monthly converts">
                                                <div className="head">
                                                    <h2 className="heading">This Month</h2>
                                                    <button>+</button>
                                                </div>
                                                {
                                                    this.state.monthlyConverts.map(investor=>{
                                                        return (
                                                            <div className="investor">
                                                                <p>{investor.Name}</p>
                                                                <p>{investor["User name"]}</p>
                                                                {
                                                                    investor.invplan?
                                                                    <p>₦{investor.invplan}</p>:
                                                                    <p>₦38,000</p>
                                                                }
                                                                <p>{investor["Registration Date"]}</p>
                                                            </div>
                                                        )
                                                    })
                                                }
                                            </div>

                                    </section>
                                </Fade>:null
                            }
                        </div>
                    }

                    {/* Leader board */}
                                                                    {/* ACCNO: 
                                                "2254584192"
                                                BANKNAME: 
                                                "ZENITH"
                                                DESCRIPTION: 
                                                "HOM"
                                                DOB: 
                                                "22ND NOV"
                                                DURATION: 
                                                ""
                                                LOCATION: 
                                                "CHALLENGE"
                                                NAME: 
                                                "SEWEJE NIKE"
                                                PHONENO: 
                                                "8039545826"
                                                SCREEN: 
                                                ""
                                                STATUS: 
                                                "ACTIVE"
                                                TEAM: 
                                                "F"
                                                USERNAME: 
                                                "FPT004" */}

                    <section className="leaderboard">
                        <input onFocus={()=>this.setState({openAdmin: false})} placeholder="🔍 Search" onChange={this.filterList}/>
                        <p className="lilength">{filterSearch.length}</p>
                        {
                            filterSearch.map(admin=>{
                                return (
                                    <div className="marketer" onClick={()=>this.getCurrentTotal(admin)}>
                                        <div className="img">
                                                {
                                                    this.state.isLoadingpic?

                                                    <img className="loader" src={require('../img/loader.jpeg')}/>
                                                    :
                                                    admin.url?
                                                    <img src={admin.url}/>:
                                                    <img src={require('../img/default.png')}/>
                                                }
                                        </div>
                                        <p>{admin.NAME}</p>
                                    </div>
                                )
                            })
                        }
                        
                        
                    </section>
                </section>
            </div>
        );
    }
}

export default MarketingBoard;