import React, { Component } from 'react';
import { firebaseDB } from '../firebase';
import Particles from 'react-particles-js';

class RecordBook extends Component {

    state = {
        marketers: [],
        recordbook: [],
        records: [],
        marketer: {},
        tableType: 0,
        finalReward: 0,
        finalCallerReward: 0,
        form: false,
        isLoading1: true,
        isLoading2: false,
        search1: '',
        search2: '',
    }

    componentDidMount(){
        firebaseDB.ref('admins').on('value', (snapshot)=>{
            const marketers=[]
            snapshot.forEach(marketer => {
                marketers.push(marketer.val())
            });
            this.setState({marketers, isLoading1:false})
        })
        firebaseDB.ref('recordbook').on('value', (snapshot)=>{
            const recordbook=[]
            snapshot.forEach(record => {
                recordbook.push(record.val())
            })
            this.setState({recordbook})
            setTimeout(() => {
                if(this.state.marketer.name){
                    this.openRecord(this.state.marketer)
                }
            }, 1000);
        })
    }

    convertTable = () => {
        const tableType = this.state.tableType
        if(tableType===0){
            this.setState({tableType: 1})
        }else {
            this.setState({tableType: 0})
        }
    }

    openRecord = (marketer) => {
        const records = []
        this.state.recordbook.forEach(record=>{
            if(record.officer.toLowerCase() === marketer.username.toLowerCase()){
                records.push(record)
            }
        })
        this.setState({records, marketer})
        console.log(records)
        setTimeout(() => {
            this.calculatePayment()
        }, 1000);
    }

    calculatePayment = () => {
        const records = this.state.records
        const rewards = []
        const callerRewards = []
        records.forEach(record=>{
            let reward
            if(record.source==='personal'){
                if(record.plan==='70k'){
                    reward = 5000
                }else if(record.plan==='140k'){
                    reward = 10000
                }else if(record.plan==='210k'){
                    reward = 15000
                }else if(record.plan==='280k'){
                    reward = 20000
                }else if(record.plan==='420k'){
                    reward = 25000
                }else if(record.plan==='490k'){
                    reward = 30000
                }
            }else if(record.source==='radio'){
                if(record.plan==='70k'){
                    reward = 2500
                }else if(record.plan==='140k'){
                    reward = 5000
                }else if(record.plan==='210k'){
                    reward = 7500
                }else if(record.plan==='280k'){
                    reward = 10000
                }else if(record.plan==='420k'){
                    reward = 12500
                }else if(record.plan==='490k'){
                    reward = 15000
                }
            }


            let callerReward
            if(record.plan==='70k'){
                callerReward = 2500
            }else if(record.plan==='140k'){
                callerReward = 5000
            }else if(record.plan==='210k'){
                callerReward = 7500
            }else if(record.plan==='280k'){
                callerReward = 10000
            }else if(record.plan==='420k'){
                callerReward = 12500
            }else if(record.plan==='490k'){
                callerReward = 15000
            }
            rewards.push(reward)
            callerRewards.push(callerReward)
        })
        let finalReward = 0
        rewards.forEach(reward=>{
            finalReward+=reward
        })
        let finalCallerReward = 0
        callerRewards.forEach(callerReward=>{
            finalCallerReward+=callerReward
        })
        this.setState({finalReward, finalCallerReward})
    }

    register = (e) => {
        e.preventDefault()
        const date = {
            day: new Date().getUTCDate(),
            month: new Date().getUTCMonth(),
            year: new Date().getUTCFullYear(),
        }
        this.setState({isLoading2: true})
        firebaseDB.ref('recordbook').push().set({
            name: e.target.name.value,
            plan: e.target.plan.value,
            mp: e.target.mp.value,
            source: e.target.source.value,
            officer: e.target.officer.value,
            date: `${date.day}-${date.month}-${date.year}`
        }).then(()=>{
            this.setState({isLoading2: false})
            document.querySelector('form').reset()
        })
    }

    openForm = () => {
        if(this.state.form){
            this.setState({form: false})
        }else {
            this.setState({form: true})
        }
    }

    filterList = (e) => {
        this.setState({
            search2: e.target.value.toLowerCase(),
        })
    }

    //////// Open REG  Form //////////
    render() {
        const filteredOfficerList1 = this.state.marketers.filter(e=>{
            if(e.name.toLowerCase().includes(this.state.search1)){
                return e
            }
        })
        const filteredOfficerList = this.state.marketers.filter(e=>{
            if(e.name.toLowerCase().includes(this.state.search2)){
                return e
            }
        })
        return (
            <div className="recordbook">
                <Particles className="particles" params={{
                    particles: {
                        "move": {
                            "enable": true,
                            "speed": 10,
                        },
                        number: {
                            value: 30
                        },
                    }
                }} />
                <div className="book">
                    <div className="left">
                        <input onChange={(e)=>this.setState({search1: e.target.value})} placeholder="Search"/>
                       {
                           this.state.isLoading1?
                           <img className="loader" src={require('../img/loader.jpeg')}/>
                           :
                           <ul>
                                {
                                    filteredOfficerList1.map(marketer=>{
                                        return (
                                            <li onClick={()=>this.openRecord(marketer)}>
                                                <div className="img"></div>
                                                <div>
                                                    <p className="name">{`${marketer.name.slice(0,17)}...`}</p>
                                                    {
                                                        marketer.username?
                                                        <p>{marketer.username}</p>:null
                                                    }
                                                </div>
                                            </li>
                                        )
                                    })
                                }
                            </ul>
                       }
                    </div>
                    {
                        this.state.marketer.name?
                        <div className="right">
                            <header>
                                <div className="img"></div>
                                <div>
                                    <p className="name">{this.state.marketer.name}</p>
                                    <div className="details">
                                        <p>{this.state.marketer.username}</p>
                                        <p>Team Z</p>
                                    </div>
                                </div>
                                <div className="reward">
                                    <span>M</span>‎
                                    <p>₦{this.state.finalReward}</p>
                                </div>
                                <div className="reward">
                                    <span>C</span>‎
                                    <p>₦{this.state.finalCallerReward}</p>
                                </div>
                            </header>
                            <div className="main">
                                {
                                    this.state.tableType===0?
                                    <div className="cta">
                                        <button className="clicked" onClick={this.convertTable}>A</button>
                                        <button onClick={this.convertTable}>T</button>
                                    </div>:
                                    <div className="cta">
                                        <button onClick={this.convertTable}>A</button>
                                        <button className="clicked" onClick={this.convertTable}>T</button>
                                    </div>
                                }
                                
                                <div className="table-container">
                                <table>
                                    <tbody>
                                        <tr>
                                            <th>S/N</th>
                                            <th>Date</th>
                                            <th>Name</th>
                                            <th>Plan</th>
                                            <th>MP</th>
                                            <th>Radio</th>
                                            <th>Personal</th>
                                        </tr>
                                        {
                                            this.state.records.map((record, num)=>{
                                                return (
                                                    <tr>
                                                        <td>{num+1}</td>
                                                        <td>{record.date}</td>
                                                        <td>{record.name}</td>
                                                        <td>{record.plan}</td>
                                                        <td>{record.mp}</td>
                                                        {
                                                            record.source==='radio'?
                                                            <td>✔️</td>:
                                                            <td>❌</td>
                                                        }
                                                        {
                                                            record.source==='personal'?
                                                            <td>✔️</td>:
                                                            <td>❌</td>
                                                        }
                                                    </tr>
                                                )
                                            })
                                        }

                                        
                                    </tbody>
                                </table>
                                </div>
                            </div>
                        </div>:
                        <div className="welcome">
                            <img className="logo" src={require('../img/logo.png')}/>
                            <p>Select a marketer from the list</p>
                        </div>
                    }
                </div>

                {
                    /////// Reg Form ///////
                }
                {
                    this.state.form?
                    <form className="form" onSubmit={this.register}>
                        <h2>Reg form</h2>
                        <input name="name" className="input" placeholder="Full name"/>
                        <select name="plan">
                            <option value="70k">70,000</option>
                            <option value="140k">140,000</option>
                            <option value="210k">210,000</option>
                            <option value="280k">280,000</option>
                            <option value="420k">420,000</option>
                            <option value="490k">490,000</option>
                        </select>
                        <fieldset>
                            <legend>Mode of payment</legend>
                            <label>
                                <input name="mp" value="cash" type="radio"/>
                                Cash
                            </label>
                            <label>
                                <input name="mp" value="transfer" type="radio"/>
                                Transfer
                            </label>
                        </fieldset>
                        <fieldset>
                            <legend>Source</legend>
                            <label>
                                <input name="source" value="personal" type="radio"/>
                                Personal
                            </label>
                            <label>
                                <input name="source" value="radio" type="radio"/>
                                Radio
                            </label>
                        </fieldset>
                        <div className="officer-container">
                            <input onChange={this.filterList} className="input in2" name="officer" placeholder="Account officer"/>
                                <div className="listInp">
                                   
                                    {
                                        filteredOfficerList.map(e=>{
                                            return (
                                                <div className="inpList" onClick={()=> {
                                                    if(e.username){
                                                        document.querySelector('.in2').value=e.username
                                                    }else {
                                                        document.querySelector('.in2').value=e.name
                                                    }
                                                }}>
                                                    <span>{e.username}</span>
                                                    <p>{e.name}</p>
                                                </div>
                                            )
                                        })
                                    }
                                </div>
                            
                          
                        </div>

                        <div style={{display:'flex', alignItems:'center'}}>
                            <button>Submit</button>
                            {
                                this.state.isLoading2?
                                <img className="loader2" src={require('../img/loader.jpeg')}/>
                                :null
                            }
                        </div>

                    </form>:
                    <form className="form" style={{transform: 'translateX(50%) translateY(50%) scale(0)', height: '50vh'}}></form>
                }
                <div className="addnew">
                    {
                        this.state.form?
                        <button style={{transform: 'rotate(405deg)', color: 'red', fontWeight: '100'}} onClick={this.openForm}>+</button>:
                        <button onClick={this.openForm}>+</button>
                    }
                </div>
            </div>
        );
    }
}

export default RecordBook;




