import React from 'react';
import { Link, Redirect } from 'react-router-dom';
import { firebaseAuth } from '../firebase';


const sendResetEmail = (e) => {
    e.preventDefault()
    firebaseAuth.sendPasswordResetEmail(e.target.email.value).then(()=> {
        const paragraph = document.createElement('p')
        paragraph.textContent = 'We have sent a password reset link to your mail'
        document.querySelector('.reset_password').append(paragraph)
      }).catch((error)=> {
        const paragraph = document.createElement('p')
        paragraph.textContent = error.message
        document.querySelector('.reset_password').append(paragraph)
      });
}

const ResetPassword = () => {
    return (
        <div className="reset_password">
            <form className="form" onSubmit={sendResetEmail}>
                <h1 className="heading">Freedom Synergy pro</h1>
                <div className="link"><Link  to="/">&larr; Back</Link></div>
                <h2 className="heading_small">Reset Your password</h2>
                <label>
                    Email
                    <input name="email" required placeholder="Email address"/>
                </label>
                <button>Reset password</button>
            </form>
        </div>
    );
};

export default ResetPassword;