import React from 'react';

const DaysLeft = ({daysLeft}) => {
    
    return (
        <div className="duedate_info">
            {
                daysLeft>1?
                <p>{daysLeft} days</p>:
                daysLeft===1?
                <p>Tomorrow</p>:
                daysLeft===0?
                <p>Today</p>:
                daysLeft===-1?
                <p>Yesterday</p>:
                <p>{Math.abs(daysLeft)} days ago</p>
            }
        </div>
    );
};

export default DaysLeft;