import React, { Component } from 'react';
import FontAwesome from 'react-fontawesome';
import { PrimaryBtn } from '../Components/Btns';
import { Redirect, Link } from 'react-router-dom';
import Fade from 'react-reveal/Fade';
import Particles from 'react-particles-js';

class LandingPage extends Component {
    
    render() {
        
        return (

            <div id="landing_page" className="landing_page">

      
            <Particles className="particles" params={{
                    particles: {
                        "move": {
                            "enable": true,
                            "speed": 10,
                        },
                        number: {
                            value: 30
                        },
                    }
                }} />
            
                {
                    // auth checker //
                    this.props.isAuthenticated?
                    <Redirect to="/admin"/>:
                    <Redirect to="/"/>
                }

                {/* <div className="independence">
                    <img src={require('../img/ngflag.gif')}/>
                    <p>Nigeria is 60yrs</p>
                </div> */}
                <form onSubmit={this.props.signin} className="signin">
                    <header>
                        <img alt="logo" className="logo" src={require('../img/logo.png')}/>
                        <h1 className="heading">Freedom <span>Synergy pro</span></h1>
                    </header>
                    <div className="form">
                        <div>
                            <FontAwesome className="font" name="user-circle" size="2x"/>
                            <input name="email" placeholder="Email" className="input"/>
                        </div>
                        <div>
                            <FontAwesome  className="font" name="lock" size="2x"/>
                            <input name="password" type="password" placeholder="Password" style={{marginLeft: '2.5rem'}} className="input"/>
                        </div>
                        <div className="cta">
                            {
                                this.props.isLoading?
                                 <img className="loader" src={require('../img/loader.jpeg')}/>:null
                            }
                            <PrimaryBtn title='Sign in'/>
                        </div>
                        <p>Unable to signin or forgot <Link className="link" to="/resetpassword">password?</Link></p>

                    </div>
                    
                </form>
                {
                    this.props.error?
                        <Fade right>
                        <div className="error">
                            <p>{this.props.error}</p>
                        </div>
                        </Fade>:null
                    }
            </div>
        );
    }
}

export default LandingPage;