import React, { Component } from 'react';
import { firebaseDB } from '../firebase';
import CurrencyFormat from 'react-currency-format';
import moment from 'moment';
import business from 'moment-business-days';


class Yakata extends Component {

    state = {
        yakata: [],
        balance: 0,
        totaldue: 0,
        plans: [],
        amount: 0,
        amountRequired: 0,
        newAmount: 0,
        d: []
    }

    componentDidMount(){
        const yakata = []
        this.getRoi()
        firebaseDB.ref('investors').once('value').then(e=>{
            console.log(e.val())
            e.forEach(e=>{
                if(e.val()["User name"] && e.val()["User name"].toLowerCase().includes('fspy') || e.val().accountType==="yakata"){
                    yakata.push({...e.val(), key: e.key})
                }
                this.setState({yakata})
            })
            setTimeout(() => {
                this.getTotalEarning()
                //
                setTimeout(() => {
                    this.getTotal()
                    this.getAmountSpent()
                    setTimeout(() => {
                        setTimeout(() => {
                            this.getRequiredAmount()
                        }, 700);
                    }, 1000);
                }, 1000);
            }, 1000);
        })

        fetch(`https://api.paystack.co/balance`, {
            method: 'get',
            headers: {
                'Authorization': `Bearer sk_live_3fd598928632736eaaa7467f3b428ca78ae02cfb`
            }
        }).then(e=>{
            return e.json()
            .then(e=>{
                console.log(e.data[0].balance)
                const balance = e.data[0].balance
                this.setState({balance})
                })
            }).catch(e=>{
                console.log(e)
                
            })
    }

    getRoi = () => {
        firebaseDB.ref('plans').once('value').then(e=>{
            this.setState({plans: e.val()})
            console.log(e.val())
        })
    }

    getTotalEarning = () => {
        const yakata = this.state.yakata

        yakata.forEach((e, i)=>{
            const regDate = e["Registration Date"]
            const days = (e.Stage+1)*30
            const holidays = [
                '2020-01-01','2020-04-10','2020-04-13','2020-05-01','2020-05-25','2020-05-26','2020-06-12','2020-07-30','2020-07-31','2020-10-01','2020-10-29','2020-12-25','2020-12-26'    
            ]
            moment.updateLocale('us', {
                holidays,
                holidayFormat: 'YYYY-MM-DD'
            });

            let ue = moment(business(regDate, 'DD-MM-YYYY').businessAdd(days)._d)  
            
            
            if(moment(ue).isSame(moment(), 'day') || moment(ue).isBefore(moment(), 'day')){
                // alert('you\'ve been credited ')
                // firebaseDB.ref('investors').child(this.props.userKey).update({
                //     Stage: this.state.investor.Stage+1
                // })
                const stage = e.Stage+1
                const invplan = e.invplan
                let plans = this.state.plans

                // calculating R.O.I
                let roi = plans["p_38k"]
                console.log(roi)

                if(invplan && invplan !== 'p_600k' && invplan !== 'p_550k' && invplan !== 'p_10k'){ 
                    roi = plans[invplan]
                }
                console.log(roi, e)

                let amount = 0
                roi.forEach((e,i)=>{
                    
                    if(i<stage){
                        amount+=roi[i]
                    }

                })
                setTimeout(() => {
                    yakata[i].totalEarnings = amount
                }, 200);


            }else {
                // alert('not due')
                const stage = e.Stage
                const invplan = e.invplan
                let plans = this.state.plans

                // calculating R.O.I
                let roi = plans["p_38k"]
                if(invplan && invplan !== 'p_600k' && invplan !== 'p_550k' ){ 
                    roi = plans[invplan]
                }
                console.log(roi, e)
                let amount = 0
                roi.forEach((e,i)=>{
                    
                    if(i<stage){
                        amount+=roi[i]
                    }

                })
                setTimeout(() => {
                    yakata[i].totalEarnings = amount
                }, 200);
            }
            this.setState({yakata})
        })
    }

    getAmountSpent = () => {
        const yakata = this.state.yakata
        yakata.forEach((e,i)=>{
            let transactions = []
            yakata[i].transactionTotal=0
            const totalEarnings = e.totalEarnings
            yakata[i].balance = totalEarnings
            if(e.stop){
                yakata[i].balance = 0
            }else if(e.payments && e.totalEarnings>0 && e.stop !== true){
                transactions = Object.values(e.payments)
    
                transactions.forEach(e=>{
                    if(e.transfer_code){
                        fetch(`https://api.paystack.co/transfer/${e.transfer_code}`, {
                            method: 'get',
                            headers: {
                                'Authorization': `Bearer sk_live_3fd598928632736eaaa7467f3b428ca78ae02cfb`
                            }
                        }).then(e=>{
                            return e.json()
                            .then(e=>{
                                const amount = e.data.amount
                                if(e.data.status=== "failed"){
                                    yakata[i].transactionTotal+=0
                                }else{
                                    yakata[i].transactionTotal+=(amount/100)
                                }
                                

                                yakata[i].balance = totalEarnings - yakata[i].transactionTotal
                                this.setState({amountRequired: this.state.amountRequired+yakata[i].balance})
                                    this.setState({yakata})
                                console.log(yakata[i], yakata[i].balance)
                            })
                        }).catch(e=>{
                            console.log(e)
                            
                        })
                    }else {
                        yakata[i].balance = totalEarnings - 0
                    }
                })
            }

           
        })
    }

    getBalance = () => {
        const d = []
        let newAmount = 0
        this.state.yakata.forEach((e,i)=>{
            
            if(e.balance>0){
                d.push(e)
                newAmount+= e.balance
            }else{

            }
            this.setState({newAmount, d})
            console.log(d)
        })
    }

    getTotal = () => {
        let amount = 0
        this.state.yakata.forEach(e=>{
            amount+=e.totalEarnings
            console.log(e, amount)
        })
        this.setState({amount})
    }

    getRequiredAmount = () => {
        let amountRequired = 0
      
    }

    
    render() {
        return (
            <div className="yakata">
               <header>
                   <div>
                        <h3>Sum Total</h3>
                        <span><CurrencyFormat value={parseFloat(this.state.amount)} displayType={'text'} thousandSeparator={true} prefix={'₦'} renderText={value => <div className="amount" >{value}</div>} /></span>
                   </div>
               
                   <button onClick={this.getBalance}>Calculate amount</button>
                   <h1>{this.state.newAmount}</h1>
                   <div className="balance">
                       <h2>Balance</h2>
                       <span><CurrencyFormat value={parseFloat(this.state.balance/100)} displayType={'text'} thousandSeparator={true} prefix={'₦'} renderText={value => <div className="amount" >{value}</div>} /></span>
                   </div>
               </header>

               {
                   this.state.yakata.map(e=>{
                       return <div className="inv">
                           <p>{e["Registration Date"]}</p>
                           <p>{e.Name}</p>
                           <p>{e["User name"]}</p>
                           <p>{e.invplan}</p>
                           <p>{e.totalEarnings}</p>
                           <p>{e.balance}</p>
                       </div>
                   })
               }
            </div>
        );
    }
}

export default Yakata;