import React, { Component } from 'react';
import FontAwesome from 'react-fontawesome';
import Overview from './Overview'
import StageProgress from './StageProgress'
import UpdateProgress from './UpdateProgress'
import EditProfile from './EditProfile'
import ProfilePic from '../Components/ProfilePic';
import Alert from '../Components/Alert';
import { Link, Redirect } from 'react-router-dom';
import { firebaseDB } from '../firebase';
import moment from 'moment';
import business from 'moment-business-days'
import PaymentHistory from './PaymentHistory';
import ChangeBank from './ChangeBank';

class Dashboard extends Component {

    state = {
        user: this.props.user,
        page: 'overview',
        finalDate: 0,
        alert: {
            on: false,
            type: '',
            message: ''
        },
        stars: [],
        openBox: false,
        loader: '',
        logout: false,
        dueDates: []
    }


    componentDidMount(){
        const date = {
            day: new Date().getUTCDate(),
            month: new Date().getUTCMonth()+1,
            year: new Date().getUTCFullYear()
        }
        this.calculateCurrentStage()
        this.getStars() /// on complete investment 

       
    }

    getStars = () => {
        const star = this.state.user.star
            const stars = []
        for(let i=0; i<star; i++){
            stars.push('⭐️')
        }
        this.setState({stars})
    }

    ////// after completing an investment plan
    resumeInvestment = (e) => {
        e.preventDefault()
        this.setState({loader: 'loading...'})
        
        let date = e.target.date.value.split('-')
        const regDate = `${date[2]}-${date[1]}-${date[0]}`
        console.log(date, e.target)

        const accountType = e.target.accountType.value
        const invplan = e.target.plan.value

        
        firebaseDB.ref(`investors/${this.state.user.key}`).update({
            resumeInvestment: true,
            ["Registration Date"]: regDate,
            previousRegDate: this.state.user["Registration Date"],
            accountType,
            invplan
        }).then(a=>{
            const workingDays = 30
            let n = 1
            let days
            console.log(accountType)
            if(accountType==='bumper'){
                for(n; n<=9; n++) {
                    days = (workingDays*n)
                    this.getDates(regDate, days)
                }
            }else {
                for(n; n<=8; n++) {
                    days = (workingDays*n)
                    this.getDates(regDate, days)
                }
            }
            
            this.sendWelcomingMessage(this.state.user.Name, this.state.user["Phone Number"])
            
        }).catch(e=>{
            this.setState({loader: 'failed'})
            this.alert('negative', e.message)
        })
    }

    ////// mini router
    changePage = (page) => {
        this.setState({page})
    }

    ///// current stage of investor/////
    calculateCurrentStage = ()=>{
        const user = this.state.user
        const currentStage= Math.ceil(
            (parseInt(user["Stage 1"]) + parseInt(user["Stage 2"]) + parseInt(user["Stage 3"]) + parseInt(user["Stage 4"]) +
            parseInt(user["Stage 5"]) + parseInt(user["Stage 6"]) + parseInt(user["Stage 7"]) + parseInt(user["Stage 8"]) + parseInt(user["Stage 9"]))/9)
            this.setState({currentStage})
    }

    //// alers for server response /////
    alert = (type, message)=> {
        this.setState({alert: {
            on: true,
            type,
            message
        }})
        setTimeout(() => {
            this.setState({alert:{
                on: false
            }})
        }, 5000);
    }

    logout = () => {
        this.setState({logout: true})
        this.props.openDashboard(false)
    }


    ///// getting new due dates 
    getDates = (regDate, days)=>{  

        const holidays = ['2019-01-01','2019-02-22','2019-04-19','2019-04-22','2019-05-01','2019-05-29','2019-06-04','2019-06-05','2019-06-12','2019-08-12','2019-08-13','2019-10-01','2019-11-9','2019-11-11','2019-12-25','2019-12-26',
            '2020-01-01','2020-04-10','2020-04-13','2020-05-01','2020-05-25','2020-05-26','2020-06-12','2020-07-30','2020-07-31','2020-10-01','2020-10-29','2020-12-25','2020-12-26'    
        ]
        moment.updateLocale('us', {
            holidays,
            holidayFormat: 'YYYY-MM-DD'
        });
        

        let ue
        // m.add('d', days)

        ue = moment(business(regDate, 'DD-MM-YYYY').businessAdd(days)._d).isoWeekday(5)
        
        let upcomingEarning = {
            day: ue.get('date'),
            month: ue.get('month')+1,
            year: ue.get('year'),
        }        

        this.state.dueDates.push(`${upcomingEarning.day}-${upcomingEarning.month}-${upcomingEarning.year}`)
        console.log(upcomingEarning)
        console.log(this.state.dueDates)
    }

    sendWelcomingMessage = (fullname, number) => {

            const dueDates = this.state.dueDates

            let dates = ''
            dueDates.forEach(date=>{
                dates += ` ${date}, `
            })

            console.log(dates, number)
            const data = `sender=FSP Nigeria&to=${number}&message=Hello ${fullname}, thank you for resubscribing.
            Your new due dates are: ${dates}%20SMS&type=0&routing=5&token=ncGtBP4FAd6V5Jv81aoUl7S3sGIVX3e0XaKHGyEvfSifpMnu4IZaHX6FCD0mJbbpN2WUS7xgAC8EDdYis8ZqbhdqdbKAU4z0KV1b`;

            const xhr = new XMLHttpRequest()

            const thisPointer = this
            xhr.addEventListener("readystatechange", function () {
            if (this.readyState === this.DONE) {
                console.log(this.responseText);
                console.log(this.responseType);
                if(this.response.includes('"error":true')){
                    thisPointer.alert('negative', 'Unable to send due dates')
                }else {
                    thisPointer.alert('positive', 'Due dates sent successfully')
                    setTimeout(() => {
                        thisPointer.logout()
                    }, 1000);
                }
            }
            });

            xhr.open("POST", "https://smartsmssolutions.com/api/json.php")
            xhr.setRequestHeader("content-type", "application/x-www-form-urlencoded");

            xhr.send(data)

           

    }
    
    render() {
        const user = this.state.user
        const admin = this.props.admin
        const stage = this.props.stage
        
        return (
            <div className="dashboard">
               
                
                {
                    ///// redirect after completing an investment plan
                    this.state.loader==='done'?
                    <Redirect to="/admin"/>:null
                }
                {
                    ///// alert model
                    this.state.alert.on?
                    <Alert type={this.state.alert.type} 
                    message={this.state.alert.message}/>:null
                }


                {/* dashboard header */}
                <header className="header">
                    <img alt="logo" className="logo" src={require('../img/logo.png')}/>
                    <h1 className="heading">Freedom Synergy pro</h1>
                </header>
                {
                    /// this will display when the user complletes an inv and hasn't resumed ///
                    user.resumeInvestment===false?
                        <div className="completed_inv">
                            <div className="stars">
                                {
                                    this.state.stars.map(star=>{
                                        console.log(star)
                                        return <span>{star}</span>
                                    })
                                }
                            </div>
                            <h2>This user has completed an investment plan</h2>
                            <p className="signature">Confirmed by <span>{user.admin}</span></p>
                            {
                                !this.state.openBox?
                                <button onClick={()=>this.setState({openBox:true})}>Restart new plan</button>:null
                            }
                            {
                                this.state.openBox?
                                <form onSubmit={this.resumeInvestment}>
                                    <input required type="date" name="date"/>
                                    <h5>Select a new investment plan</h5>
                                    <select style={{margin: '1rem 0'}} required name="accountType">
                                        <option value="">Plan type</option>
                                        <option value="yakata">YAKATA</option>
                                        <option value="bumper">BUMPER</option>
                                    </select>
                                    <select required name="plan">
                                        <option value="">Plan</option>
                                        <option value="p_50k">50000</option>
                                        <option value="p_150k">150000</option>
                                        <option value="p_100k">100000</option>
                                        <option value="p_200k">200000</option>
                                        <option value="p_250k">250000</option>
                                        <option value="p_300k">300000</option>
                                        <option value="p_350k">350000</option>
                                        <option value="p_400k">400000</option>
                                        <option value="p_450k">450000</option>
                                        <option value="p_500k">500000</option>
                                    </select>
                                    {
                                        admin.accountType==='readandwrite'?
                                        <button>Let's Go</button>:
                                        null
                                    }
                                </form>:null
                            }
                        </div>
                    :
                    <main>

                            {/* this will display when the user is currently receiving payments */}

                            <section className="sidebar">
                            <ProfilePic alert={this.alert} user={user}/>
                            <h2 className="heading2">{user["User name"]}</h2>
                            <div className="stars">
                                {
                                    this.state.stars.map(star=>{
                                        console.log(star)
                                        return <span>{star}</span>
                                    })
                                }
                            </div>
                            <nav className="nav">
                                <ul className="list">
                                    {
                                        this.state.page==='overview'?
                                        <li className="clicked"  onClick={()=>this.changePage('overview')}>
                                        <FontAwesome className="font" name="street-view"/><span className="text">overview</span></li>
                                        :
                                        <li className="overview"  onClick={()=>this.changePage('overview')}>
                                        <FontAwesome className="font" name="street-view"/><span className="text">overview</span></li>
                                    }
                                    {
                                        admin.accountType === 'readonly' || admin.email === undefined?
                                        <span className="unavailable">.</span>:
                                        this.state.page==='update'?
                                        <li className="clicked"  onClick={()=>this.changePage('update')}>
                                        <FontAwesome className="font" name="pencil-square-o"/><span className="text">Update progress</span></li>
                                        :
                                        <li className="update"  onClick={()=>this.changePage('update')}>
                                        <FontAwesome className="font" name="pencil-square-o"/><span className="text">Update progress</span></li>
                                    }
                                    {
                                        this.state.page==='history'?
                                        <li className="clicked"  onClick={()=>this.changePage('history')}>
                                        <FontAwesome className="font" name="street-view"/><span className="text">Payment History</span></li>
                                        :
                                        <li className="overview"  onClick={()=>this.changePage('history')}>
                                        <FontAwesome className="font" name="street-view"/><span className="text">Payment History</span></li>
                                    }
                                    {/* {
                                        this.state.page==='bank'?
                                        <li className="clicked"  onClick={()=>this.changePage('bank')}>
                                        <FontAwesome className="font" name="street-view"/><span className="text">Edit Bank details</span></li>
                                        :
                                        <li className="overview"  onClick={()=>this.changePage('bank')}>
                                        <FontAwesome className="font" name="street-view"/><span className="text">Edit Bank details</span></li>
                                    } */}
                                    {
                                        admin.email === undefined || admin.accountType === 'readonly' && admin.email !== 'uthmanyahya2@gmail.com' ?
                                        <span className="unavailable">.</span>:
                                        this.state.page==='edith'?
                                        <li className="clicked"  onClick={()=>this.changePage('edith')}>
                                        <FontAwesome className="font" name="wrench"/><span className="text">Edit profile</span></li>
                                        :
                                        <li className="edith"  onClick={()=>this.changePage('edith')}>
                                        <FontAwesome className="font" name="wrench"/><span className="text">Edit profile</span></li>
                                    }
                                </ul>
                            </nav>
                        </section>

                        {/* link back to homepage */}
                        {
                            admin.email?
                            <button onClick={this.logout} className="link">&larr; Back</button>:
                            <button className="link" onClick={this.logout} >&larr; Logout</button>
                        }

                        {/* content to be displayed */}
                        <section className="content">
                            {
                                this.state.page==='overview'?
                                    <Overview admin={admin} holidays2019={this.props.holidays2019} holidays2020={this.props.holidays2020}  finalDate={this.state.finalDate}
                                     user={user} stage={stage} currentStage={this.state.currentStage}/>:
                                this.state.page==='progress'?
                                    <StageProgress currentStage={this.state.currentStage} user={user}/>:
                                this.state.page==='update'?
                                    <UpdateProgress alert={this.alert} user={user} stage={stage} currentStage={this.state.currentStage}/>:
                                    this.state.page==='bank'?
                                        <ChangeBank alert={this.alert} user={user} stage={stage} currentStage={this.state.currentStage}/>:
                                this.state.page==='history'?
                                    <PaymentHistory alert={this.alert} user={user}/>:
                                    <EditProfile admin={admin} alert={this.alert} user={user}/>
                            }
                        </section>
                    </main>
                }
            </div>
        );
    }
}

export default Dashboard;